import React, { Component } from 'react';

import '../css/contact.scss'
import Upload from '../js/upload';

import MobileCheck from '../js/mobileCheck.js';

class ContactHire extends Component {

  constructor(props){
    super(props);
    this.state = {
      allIn: false,
      cheked: false,
      isTarea: false,
      isProcess: false,
      isMobile : false,
      isValue: false
    };

    this.btnNext = document.getElementById('btnNext');
    this.btnSend = document.getElementById('btnSend');
    this.body = document.querySelector('body');
    this.nextForm = '';
  }


  componentDidMount(){

    MobileCheck(this);

    // file upload js
    Upload();

    const autoSize = document.querySelectorAll('.autosize');
    
    Array.prototype.forEach.call(autoSize, (el, i) => {
      const spans = document.createElement('span');
          spans.classList.add('fake');
      el.parentNode.appendChild(spans);

      // resize     fakewidth 
      window.addEventListener('resize', () => {
        let fakeWid = el.parentNode.querySelector('.fake').clientWidth;
        el.style.width = fakeWid + 'px';
      });
    });

    const scDown = document.querySelector('.cnt_project').offsetTop;
    const headerH = document.querySelector('header').offsetHeight;
    if(scDown > 100){
      window.scrollTo({'top':scDown+headerH});
    }

    document.querySelector('#companyName').focus();

  }


  componentDidUpdate(){
    
    // contact fix  
      if(this.state.isMobile){
        this.body.classList.add('fix');
      } else{
        this.body.classList.remove('fix');
      }
  }

  componentWillUnmount(){
    window.scrollTo({'top':0});
    this.body.classList.remove('fix');
  }


  handleChange(e){

    if(!this.state.isProcess){

      // console.log('', e.target.value);
      const nextArea = e.target.parentNode.nextElementSibling;

      //  ipt_wrap  
      if(nextArea.classList.contains('ipt_wrap')){

        if(!e.target.value == ''){
          // console.log(' ');
          //    

            nextArea.classList.add('show');

          // M - nextBtn 
          // if(this.state.isMobile){
          //   this.setState({
          //     isValue: true
          //   });
          //   this.nextForm = nextArea;
          // } else {
          //   nextArea.classList.add('show');
          // }
    
        } else {
          // console.log(' ');
          //     
          //    
          const nextVal = nextArea.querySelector('.ipt').value;
          if(nextVal == ''){

            nextArea.classList.remove('show');

            // M - nextBtn 
            // if(this.state.isMobile){
            //   this.setState({
            //     isValue: false
            //   });
              
            // } else {
            //   nextArea.classList.remove('show');
            // }
            
          }
        }
      }
    }

    const iptEl = document.getElementsByClassName('ipt');
    const iptElLeng = iptEl.length;
    let valLeng = iptElLeng;

    //   check
    Array.prototype.forEach.call(iptEl, (el, i) => {
      // console.log(el.querySelector('.ipt').value, i);

      if(el.value == ''){
        valLeng = valLeng - 1;
      }


      // resize input
      // let iptVal = el.querySelector('.ipt').value;
      // let valLeng = iptVal.length;
      // console.log(i + ':' + valLeng );
      // el.querySelector('.ipt').style.width = valLeng*int + 50 + 'px';
    });

    // fake Input
    if(e.target.classList.contains('autosize')){
      const fakeIpt = e.target.parentNode.querySelector('.fake');
      fakeIpt.textContent = e.target.value;

      let fakeWidth = fakeIpt.clientWidth;
      e.target.style.width = fakeWidth + 'px';

      // console.log(fakeWidth);
    };


    // console.log(valLeng);
    // Constructor   bind(this) 

    if(valLeng == iptElLeng){
      this.setState({
        allIn : true
      });
      // console.log('   ');
    } else {
      this.setState({
        allIn : false
      });
    }

    //   Next  
    if(e.target.tagName == 'TEXTAREA'){
      // console.log(' ');
      this.setState({
        isTarea: true,
        isProcess: true
      });

      let tareaLength = e.target.value.length;
      const enteredText = document.querySelector('.leng_check').querySelector('.now');
      enteredText.textContent = tareaLength;

      // Textarea Resize
      // console.log(e.target.scrollHeight);
      e.target.style.height = '100px';
      e.target.style.height = (10 + e.target.scrollHeight) +'px';

      //  &  
      document.getElementById('checkAgree').classList.add('show');
      document.getElementById('fileUpload').classList.add('show');

    }

  }

  handleFocus(e){
    const entInput = e.target.parentNode;
    entInput.classList.add('focus');
    // console.log('focus');


    // fake Input
    if(e.target.classList.contains('autosize')){
      const fakeIpt = e.target.parentNode.querySelector('.fake');
      fakeIpt.classList.add('focus');
      fakeIpt.textContent = e.target.value;
  
      let fakeWidth = fakeIpt.clientWidth;
      e.target.style.width = fakeWidth + 'px';
    };

    //        
    //       
    //          
    const nextArea = e.target.parentNode.nextElementSibling;
    let focusValLeng = e.target.value.length;

    if(focusValLeng > 0 && nextArea.classList.contains('ipt_wrap')){
        nextArea.classList.add('show');
    }

    // Textarea Resize
    if(e.target.tagName == 'TEXTAREA'){
      e.target.style.height = '100px';
      e.target.style.height = (10 + e.target.scrollHeight) +'px';
    }

  }

  handleBlur(e){
    const entInput = e.target.parentNode;

        
    // tel number hyphen
    // if(e.target.getAttribute('id') == 'telNum'){
    //   let hp = e.target.value;
    //   let newHp = hp.replace(/(^02.{0}|^01.{1}|[0-9]{3})([0-9]+)([0-9]{4})/,"$1-$2-$3");
    //   console.log(newHp);
    //   e.target.value = newHp;
    // }
    
    // fake Input
    if(e.target.classList.contains('autosize')){
      const fakeIpt = e.target.parentNode.querySelector('.fake');
      fakeIpt.classList.remove('focus');
      // fakeIpt.textContent = e.target.value;

      let _target = e.target;

      //focus     
      setTimeout(() => {
        let fakeWidth = fakeIpt.clientWidth;
        _target.style.width = fakeWidth + 'px';
      },50)
    };

    // Textarea Resize
    if(e.target.tagName == 'TEXTAREA'){
      e.target.style.height = '100px';
      e.target.style.height = (10 + e.target.scrollHeight)/1.36 +'px';
    }

    // console.log('', e.target.value);
    entInput.classList.remove('focus');

    
    if(!e.target.value == ''){
      //   
      //   
      entInput.classList.add('entered');
    } else {
      //    (   )
      //   

      if(!this.state.isProcess){
        entInput.classList.remove('show');
        entInput.classList.remove('entered');
      }

      /*
      1.   Next   O
            Next   O
            (isProcess) O

      2.         O
            UX     O
            O
      */
    }
  }

  handleCheck(e) {
    if(e.target.checked){
      this.setState({
        checked: true
      });
    } else {
      this.setState({
        checked: false
      });
    }
  }

  // Mobile Next Step
  nextStep(e){
    console.log('next');
    this.nextForm.classList.add('show');
    // setTimeout(() => {
    //   this.nextForm.querySelector('input[type=text]').focus();
    // },50);

    if(this.nextForm.querySelector('input[type=text]')){
      setTimeout(() => {
        this.nextForm.querySelector('input[type=text]').focus();
      },50);
    }
    if(this.nextForm.querySelector('textarea')){
      setTimeout(() => {
        this.nextForm.querySelector('textarea').focus();
      },50);
    }
    
    this.setState({
      isValue: false
    });
  }


  render() {
    return (
      <React.Fragment>

        <div className="contact_wrap">
          <div className="contact">

            <div className="ipt_wrap default show">
              
              <input type="text" id="companyName" className="ipt autosize" onChange={this.handleChange.bind(this)} onFocus={this.handleFocus.bind(this)} onBlur={this.handleBlur.bind(this)} />
              .
            </div>

            <div className="ipt_wrap">
               
              <input type="text" className="ipt autosize" onChange={this.handleChange.bind(this)} onFocus={this.handleFocus.bind(this)} onBlur={this.handleBlur.bind(this)} />
              .
            </div>

            <div className="ipt_wrap">
              
              <input type="text" id="telNum" className="ipt autosize" onChange={this.handleChange.bind(this)} onFocus={this.handleFocus.bind(this)} onBlur={this.handleBlur.bind(this)} />
              .
            </div>

            <div className="ipt_wrap col2">
              <span></span>
              <textarea name="" id="" cols="30" rows="10" maxLength="198"
                className="ipt" onChange={this.handleChange.bind(this)} onFocus={this.handleFocus.bind(this)} onBlur={this.handleBlur.bind(this)}
              ></textarea>
              <div className="leng_check">
                <span className="now">0</span>/198
              </div>
            </div>

            <div className="ipt_wrap col2" id="fileUpload">
              <span></span>
              <div className="formwrap file">
                  <div className="btnwrap">
                    <label className="border" htmlFor="uploadBtn"><span>Upload</span></label>
                    <span className="txt">  3 .</span>
                  </div>

                  <input type="file" className="ip_file" id="uploadBtn" multiple="multiple" />
                  <div id="uploadList"></div> 
              </div>
            </div>

            <div className="ipt_wrap" id="checkAgree">
              <div className="checkwrap">
                <input type="checkbox" id="chk1" className="ipt" onChange={this.handleCheck.bind(this)} />
                <label htmlFor="chk1"><span>  </span></label>
              </div>

            </div>

          </div>

          {/* {this.state.isMobile ? 
            <button className="action" id="btnNext" disabled={!this.state.isValue} onClick={this.nextStep.bind(this)}></button>
            : null
          } */}

          {this.state.isTarea ? 
            // <button className={`${this.state.allIn && this.state.checked ? 'color' : ''} action`} disabled></button>
            <button className="action" id="btnSend" disabled={!this.state.allIn || !this.state.checked}></button>
            : null
          }
        </div>
      </React.Fragment>
    );
  }
}

export default ContactHire;
