const BannerCmp = () => {
    return (
        <React.Fragment>
            <Link to="/Contact" className="banner">
                <div className="arrow">
                    <span></span>
                    <span></span>
                    <span></span>
                </div>

                <div className="inner">
                    <p className="cate">
                        Project Request
                    </p>
                    <p className="tit">
                           ?
                    </p>
                    <div className="cont">
                        <p>
                               ? <br/>
                                    .    .
                        </p>
                    </div>
                </div>
            </Link>
        </React.Fragment>
    )
}

