import imgVisua4 from '../images/project/beautyPoint/mo-beautypoint-con-01-img-03.png';
import imgVisua5 from '../images/project/beautyPoint/mo-beautypoint-con-02-img-01.png';
import imgVisua6 from '../images/project/beautyPoint/mo-beautypoint-con-02-img-02.png';
import imgVisua7 from '../images/project/beautyPoint/mo-beautypoint-con-02-img-03.png';
import imgVisua8 from '../images/project/beautyPoint/mo-beautypoint-con-03-img-01.png';
import imgVisua9 from '../images/project/beautyPoint/mo-beautypoint-con-03-img-02.png';
import imgVisua10 from '../images/project/beautyPoint/mo-beautypoint-con-03-img-03.png';
import imgVisua11 from '../images/project/beautyPoint/mo-beautypoint-sub-copy.png';
