import React from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';

import { PrjTopCmp, PrjInfoCmp, PrjBottomCmp } from '../component';

import '../css/project.scss';

// project style import
import '../css/asiana.scss';

import projectData from './projectData.json';

// image import
import imgVisua1 from '../images/project/asiana/asiana-logo.png';
import imgVisua2 from '../images/project/asiana/asiana-top-object-pc.png';
import imgVisua3 from '../images/project/asiana/asiana-top-object-m.png';
import imgVisua4 from '../images/project/asiana/asiana-con-1-object.png';
import imgVisua5 from '../images/project/asiana/asiana-con-1-icon.png';
import imgVisua6 from '../images/project/asiana/asiana-con-2-object.png';
import imgVisua7 from '../images/project/asiana/asiana-con-2-icon.png';
import imgVisua8 from '../images/project/asiana/asiana-con-3-object.png';
import imgVisua9 from '../images/project/asiana/asiana-con-4-object.png';

import imgVisua10 from '../images/project/asiana/asiana-con-5-01-object.png';
import imgVisua11 from '../images/project/asiana/asiana-con-5-02-object.png';
import imgVisua12 from '../images/project/asiana/asiana-con-5-03-object.png';
import imgVisua13 from '../images/project/asiana/asiana-con-5-04-object.png';
import imgVisua14 from '../images/project/asiana/asiana-con-5-05-object.png';
import imgVisua15 from '../images/project/asiana/asiana-con-5-06-object.png';

import imgVisua16 from '../images/project/asiana/asiana-con-6-01-object.png';
import imgVisua17 from '../images/project/asiana/asiana-con-6-02-object.png';


class AsianaPc extends React.Component {
    constructor(props){
        super(props);

        this.idx = 2;

        this.state = {
            type: projectData[this.idx].type,
            cate: projectData[this.idx].cate,
            title: projectData[this.idx].title,
            desc: projectData[this.idx].desc,
            mission: projectData[this.idx].mission,
            partner: projectData[this.idx].partner,
            launch: projectData[this.idx].launch,
            url: projectData[this.idx].url
        }
    }

    componentDidMount(){
        AOS.init({});
    }

    render(){
        return (
            <React.Fragment>

                <PrjTopCmp
                    type={this.state.type} 
                    title={this.state.title} 
                    desc={this.state.desc} 
                    //img={this.state.img} 
                />
                <div className="AsianaWrap">
                    <div className="asianaVisual">
                        <div className="box">
                            <div data-aos="zoom-in" data-aos-duration="2000" className="logo"><img src={imgVisua1} alt=""/></div>
                        </div>
                            <div className="object01"><img src={imgVisua2} alt=""/></div>
                            <div className="object02"><img src={imgVisua3} alt=""/></div>
                    </div>
                    

                    <div className="content inner_left">
                        <div className="view_coltype">
                            <div className="left">
                                <p className="tit">challenge</p>
                            </div>
                            <div className="right">
                                <p className="desc">
                                 , (5)    ,     .
                                  /   23 9         .         UI/UX   USER   .
                                   23 , 9             Wins CMS        .
                                </p>
                                
                                <PrjInfoCmp 
                                    mission={this.state.mission}
                                    partner={this.state.partner}
                                    launch={this.state.launch}
                                    url={this.state.url}
                                />
                            </div>
                        </div>
                    </div>

                    <div className="contArea1">
                        <div className="inner">
                            <div className="object"><img src={imgVisua4} alt=""/></div>
                            <div className="txtBox">
                                <h3>PC       </h3>
                                <p>              ,           .</p>
                                <div className="icon"><img src={imgVisua5} alt="PC, Mobile Web, APP, Smart watch"/></div>
                            </div>
                        </div>
                    </div>

                    <div className="contArea2">
                        <div className="inner">
                            <div className="object"><img src={imgVisua6} alt=""/></div>
                            <div className="txtBox">
                                <h3>  2  </h3>
                                <p>          /     .</p>
                                <div className="icon"><img src={imgVisua7} alt=" +  =   :  / "/></div>
                            </div>
                        </div>
                    </div>

                    <div className="contArea3">
                        <div className="inner">
                            <div className="object"><img src={imgVisua8} alt=""/></div>
                            <div className="txtBox">
                                <h3>    </h3>
                                <p>     ,    .          , /        .</p>
                            </div>
                        </div>
                    </div>

                    <div className="contArea4">
                        <div className="inner">
                            <div className="txtBox">
                                <span>Smaart Watch</span>
                                <h3> <br />' ' </h3>
                            </div>
                            <div className="object"><img src={imgVisua9} alt=""/></div>
                        </div>
                    </div>

                    <div className="contArea5">
                        <div className="inner">
                            <div className="txtBox">
                                <span>Mobile APP</span>
                                <h3>  <br /> UI/UX </h3>
                            </div>
                            <div className="objectWrap">
                                <div className="object01"><img src={imgVisua10} alt=""/></div>
                                <div className="object02"><img src={imgVisua11} alt=""/></div>
                                <div className="object03"><img src={imgVisua12} alt=""/></div>
                                <div className="object04"><img src={imgVisua13} alt=""/></div>
                                <div className="object05"><img src={imgVisua14} alt=""/></div>
                                <div className="object06"><img src={imgVisua15} alt=""/></div>
                            </div>
                        </div>
                    </div>

                    <div className="contArea6">
                        <div className="inner">
                            <div className="txtBox">
                                <span>PC</span>
                                <h3>  <br />  </h3>
                            </div>
                            <div className="object01"><img src={imgVisua16} alt=""/></div>
                            <div className="object02"><img src={imgVisua17} alt=""/></div>
                        </div>
                    </div>


                </div>


            <PrjBottomCmp/>

            </React.Fragment>
        )
    }
}

export default AsianaPc;